function AboutUsPage(props){
  document.title = "HM Shop | About Us";
    return(
        <div className="container mx-auto px-2 xl:px-0 flex flex-col">
            <AboutUsBanner />
   <div className="w-full px-2 xl:px-0 my-2 flex flex-col rounded">
     <AboutUsTopImage />
     <AboutUsParagraph />
   </div>
  </div>
    )
}

export default AboutUsPage;